header {
  position: fixed;
  top: 0;
  width: 100%;
  font-size: 16px;
  color: #81D8D0;
  background-color: #FEFEFE;
  border-bottom: 1px solid #DDD;
  box-shadow: 1px 1px 5px #f3f3f3;
  z-index: 999;
  transition: top .5s;
}
header.hide-top {
  top: -65px;
}
header .header-title {
  display: inline-block;
  margin-left: 20px;
  cursor: context-menu;
}
header nav {
  float: right;
  margin-right: 30px;
}
header nav ul {
  height: 63px;
  margin: 0;
}
header nav li {
  display: inline-block;
  padding: 20px 15px;
  border-bottom: 2px solid transparent;
}
header nav a {
  text-decoration: none;
  color: inherit;
  transition: background, color 0.3s, 0.3s ease-in;
}
header nav a.active::after {
  transform: scaleX(1.5);
}
header nav a::after {
  content: '';
  display: inline-block;
  position: relative;
  bottom: -6px;
  width: 100%;
  height: 3px;
  background: #ACE4DF;
  transform: scaleX(0);
  transition: transform .3s;
}
header nav a:hover {
  cursor: pointer;
}
header nav a:hover::after {
  transform: scaleX(1.5);
}



