.memo {
  position: relative;
  width: 380px;
  background: #fff;
  margin: 10px;
  padding: 0 15px;
  border: 1px solid #DDD;
  border-radius: 3px;
  transition: all .3s;
  line-height: 1.8;
}
.memo:hover {
  box-shadow: 0 0 20px #ddd;
}
.memo:hover .memo-date {
  cursor: default;
  font-size: 14px;
  color: #ACE4DF;
}
.memo pre {
  white-space: normal;
  word-break: break-word;
  font-family: sans-serif;
  background: #f6f6f6;
  border-radius: 2px;
  padding: 5px 10px;
  border-left: 3px solid #b9b9b9;
}
.memo code {
  background: #f6f6f6;
  border-radius: 2px;
  padding: 5px 10px;
  word-break: break-all;
  font-family: -webkit-pictograph, "Microsoft YaHei", Verdana, sans-serif;
}
.memo a {
  color: #ff1493;
  transition: color .3s;
}
.memo a:link {
  color: #ff1493;
}
.memo a:visited {
  color: #ff90cd;
}
.memo a:hover {
  color: #ff1493;
}
.memo .memo-date {
  position: absolute;
  bottom: 2px;
  right: 5px;
  font-size: 12px;
  color: #f6f6f6;
  transition: color, font-size 0.3s, 0.3s ease-in-out;
}



