.info-wrapper {
  position: fixed;
  height: 100%;
  width: 25%;
  min-width: 300px;
  display: flex;
  align-items: center;
  background: #FEFEFE;
  border-right: 2px solid #fefaf6;
  transition: background .5s, margin .5s;
}
.info-wrapper.hide-top {
  background: #fefaf6;
}
.hide-top .info-wrapper {
  margin-top: -64px;
}
.info-wrapper .info {
  width: 100%;
}
.info-wrapper .info:hover .info-photo {
  box-shadow: 0 0px 10px 2px #DDD;
  transform: rotate(360deg);
}
.info-wrapper .info-photo {
  transition: all .6s;
}
.info-wrapper .info-photo img {
  margin-top: 50px;
  width: 150px;
}
.info-wrapper .skill-wrapper {
  margin: 10px auto;
  cursor: pointer;
  width: 66%;
  text-align: center;
}
.info-wrapper .skill-wrapper .skill-tag {
  display: inline-block;
  padding: 2px 8px;
  border: 1px solid #b9b9b9;
  border-radius: 4px;
  color: #ababab;
  margin: 5px 0;
  transition: color .3s;
}
.info-wrapper .skill-wrapper .skill-tag:hover {
  color: #ACE4DF;
  border-color: #ACE4DF;
}
.info-wrapper .info-message,
.info-wrapper .info-photo {
  text-align: center;
  margin: 0 auto;
}
.info-wrapper .info-photo {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  overflow: hidden;
  background: #fff;
  text-align: center;
}
.info-wrapper .info-message {
  vertical-align: top;
}



